<template>
  <div id="body">
    <!-- 顶部导航栏 -->
    <div class="header">
      <router-link to="/home">{{ $t("common.home") }}</router-link>
      <el-select v-model="value" @change="handleLanguageChange">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      value:'zh',
      options: [
        {
          value: "zh",
          label: "简体中文",
        },
        {
          value: "zh-TW",
          label: "繁體中文",
        },
        {
          value: "en",
          label: "English",
        },
      ],
    };
  },
  methods: {
    handleLanguageChange(newValue) {
      this.switchLanguage(newValue);
      this.$bus.$emit('handleValue',newValue)
    },
    switchLanguage(lang) {
      this.$i18n.locale = lang;
    },
  },
};
</script>
<style>
/* 去除 el-select 的边框 */
.el-select .el-input__inner {
  width: 9rem !important;
  border: none;
  background-color:#f5f5f8 ;
  font-size: 1.4rem;
  color: #4b5563;
  font-weight: 400;
}
.el-input__inner{
  line-height: 0;
}
.el-select-dropdown__item {
  font-size: 1.4rem;
   color: #4b5563 !important;
  font-weight: 400 !important;
}
.el-select-dropdown__item {
  width: 10rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
<style scoped>
/* #region 头部开始 */
.header {
  height: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header a {
  font-size: 1.4rem;
  color: #4b5563;
  font-weight: 400;
}
.header-languge {
  margin: 0 3rem;
}
/* #endregion 头部结束 */
</style>