import Home from '../pages/Home'
import Privacy from '../pages/Privacy'
import Service from '../pages/Service'
import Answers from '../pages/Answers'

export default [
    {
        path:'/home',
        component:Home,
        name:'home'
    },
    {
        path:'/privacy',
        component:Privacy,
        name:'privacy'
    },
    {
        path:'/service',
        component:Service,
        name:'service'
    },
    {
        path:'/answers',
        component:Answers,
        name:'answers'
    },
    {
        path:'*',
        redirect: '/home'
    }
]
