<template>
  <div id="body">
    <div class="content">
      <div class="container">
        <div class="t1">
          <h1>{{ $t("content.title") }}</h1>
          <p>{{ $t("content.description") }}</p>
          <div class="messageandtip">
            <textarea
              name="message"
              id="message"
              :placeholder="$t('content.textareaPlaceholder')"
              v-model="question"
            ></textarea
            ><!-- 问题输入框 -->
            <div v-if="showTip" class="tip">{{ $t("content.tipMessage") }}</div>
            <!-- 没有问题时提示一句话 -->
          </div>
          <div v-loading="loading">
            <img
              src="./images/crystal-ball.webp"
              alt="#"
              @click="getAnswer"
            /><!-- 点击图片获取答案 -->
          </div>
          <div class="answer">
            <h2>{{ $t("content.answerTitle") }}</h2>
            <span v-if="!answer">{{ $t("content.answerWaiting") }}</span
            ><!-- 显示答案的地方 -->
            <span v-if="answer" class="newanswer">{{ answer }}</span>
          </div>
          <div class="AI">
            <h2>{{ $t("content.aiTitle") }}</h2>
            <span v-if="!aiGeneratedText">{{ $t("content.aiWaiting") }}</span><!-- AI解读的地方 -->
            <span v-if="aiGeneratedText" class="aiGeneratedText">{{
              aiGeneratedText
            }}</span>
          </div>
          <div class="welcome">
            <h2>{{ $t("content.welcomeTitle") }}</h2>
            <span>{{ $t("content.welcomeDescription") }}</span>
            <h2>{{ $t("content.faqTitle") }}</h2>
            <div class="ques">
              <ul>
                <li
                  v-for="(faq, index) in $t('content.faqQuestions')"
                  :key="index"
                >
                  {{ faq.question }}
                  <span>{{ faq.answer }}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "home",
  data() {
    return {
      question: "",
      previousQuestion: "",
      answer: "",
      loading: false,
      showTip: false,
      aiGeneratedText: "",
      apiKey: "H5zXBsnu86Dq4exGkqyDsy4s", // 替换为实际的应用API Key
      secretKey: "AkYHoLXQvPuV7eH9r1E32kC7F0CMgbjV", // 替换为实际的应用Secret Key
      accessToken: ""
    };
  },
  methods: {
    async getAccessToken() {
      const url = `/api/oauth/2.0/token?grant_type=client_credentials&client_id=${this.apiKey}&client_secret=${this.secretKey}`;
      try {
        const response = await axios.post(url, null, {
          headers: {
            "Content-Type": "application/json"
          }
        });
        this.accessToken = response.data.access_token;
        return this.accessToken;
      } catch (error) {
        console.error("获取访问令牌时出错:", error);
        throw error;
      }
    },
    async getAnswer() {
      if (!this.question) {
        this.showTip = true;
        this.loading = false;
        return;
      }
      if (this.question === this.previousQuestion) {
        this.showTip = false;
        this.loading = false;
        return;
      }
      this.showTip = false;
      this.loading = true;
      try {
        const accessToken = await this.getAccessToken();
        const apiUrl = `https://aip.baidubce.com/rpc/2.0/ai_custom/v1/wenxinworkshop/chat/ernie-speed-128k?access_token=${accessToken}`;
        const translatedAnswers = this.$t("content.answers");
        const randomIndex = Math.floor(
          Math.random() * translatedAnswers.length
        );
        this.answer = translatedAnswers[randomIndex];

        const messages = [
          {
            role: "user",
            content: `问题：${this.question}，答案：${this.answer}，请生成一段相关文字`
          }
        ];

        const data = {
          messages: messages,
          stream: false,
          temperature: 0.95, // 默认值
          top_p: 0.7, // 默认值
          penalty_score: 1.0, // 默认值
          system: "", // 可根据需要设置模型人设
          stop: [],
          max_output_tokens: 4096, // 可根据需要设置
          frequency_penalty: 0.1, // 默认值
          presence_penalty: 0.0, // 默认值
          user_id: "",
          metadata: {}
        };

        const response = await axios.post(apiUrl, data, {
          headers: {
            "Content-Type": "application/json"
          }
        });

        if (response.data.result) {
          this.aiGeneratedText = response.data.result;
        } else {
          console.error("Error in response data:", response.data);
          this.aiGeneratedText = "生成答案时出错，请重试。";
        }
        this.previousQuestion = this.question;
      } catch (error) {
        console.error("获取答案时出错:", error);
        if (error.response) {
          console.error("错误响应数据:", error.response.data);
          console.error("错误状态码:", error.response.status);
          console.error("错误头信息:", error.response.headers);
        } else if (error.request) {
          console.error("错误请求:", error.request);
        } else {
          console.error("错误信息:", error.message);
        }
        this.aiGeneratedText = "生成答案时出错，请重试。";
      } finally {
        this.loading = false;
      }
    }
  },
  created() {
    this.$bus.$on("handle", (lang) => {
      this.$i18n.locale = lang;
    });
  },
  beforeDestroy() {
    this.$bus.$off("handle");
  }
};
</script>

<style scoped>
/* #region 内容区开始 */
.content {
  width: 100%;
  margin-top: 3rem;
  height: auto;
}
.container {
  width: 60%;
  margin: auto;
}
/* ---------------- */
.t1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.container .t1 h1 {
  font-size: 2.7rem;
  color: rgb(42, 42, 154);
}
.container .t1 p {
  font-size: 1.5rem;
  margin-top: 1rem;
  color: rgb(84, 84, 240);
  flex-wrap: nowrap;
}
#message {
  width: 50vw;
  height: 12rem;
  margin-top: 3rem;
  box-sizing: border-box;
  background-color: #fff;
  padding: 20px;
  border: 1px solid rgb(247, 244, 244);
  font-size: 24px;
  font-weight: 600;
  line-height: 1.5;
  border-radius: 20px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3),
    -5px -5px 10px rgba(255, 255, 255, 0.8);
}
#message::placeholder {
  color: rgb(141, 139, 139); /* 标准语法 */
}
.messageandtip {
  margin-bottom: 4rem;
}
.t1 img {
  display: block;
  width: 50vw;
  height: 20rem;
  object-fit: cover;
  border-radius: 20px;
  transition: transform 0.5s ease;
}
.t1 img:hover {
  transform: scale(1.02);
  cursor: pointer;
}
.t1 .tip {
  color: red;
  font-size: 1.1rem;
  margin-top: 1.5rem;
  animation: shake 0.5s;
}
/* 定义抖动动画 */
@keyframes shake {
  0% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(-5px);
  }
  40% {
    transform: translateX(5px);
  }
  60% {
    transform: translateX(-5px);
  }
  80% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0);
  }
}
.t1 .answer,
.t1 .AI,
.t1 .welcome {
  box-sizing: border-box;
  width: 50vw;
  background-color: #fff;
  border-radius: 20px;
  border: 1px solid rgb(247, 244, 244);
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3),
    -5px -5px 10px rgba(255, 255, 255, 0.8);
  margin-top: 3rem;
  font-size: 26px;
  padding: 30px;
}
.t1 .welcome {
  font-size: 20px !important;
}
.answer h2,
.AI h2,
.welcome h2 {
  color: rgb(42, 42, 154);
}
.answer span,
.AI span {
  display: block;
  color: #989a9ce4;
  margin-top: 2rem;
}
.welcome span {
  display: block;
  margin: 1rem 0;
  font-size: 20px;
  color: #131518;
  font-weight: normal;
  line-height: 2;
}
.t1 .newanswer {
  color: #131518;
}
.t1 .aiGeneratedText{
  color:#24282b;
  font-size: 1.3rem;
}
.welcome .ques ul li {
  color: rgb(37, 37, 206);
  font-weight: 600;
  font-size: 22px;
  margin-top: 1rem;
}
.ques {
  margin-left: 2rem;
}
/* #endregion 第一部分内容（标题和文本框） */
</style>